import React, { useEffect } from 'react'
import { navigate } from 'gatsby'

// url : '/' (root)
const IndexPage = () => {
  useEffect(() => {
    // redirection
    navigate(process.env.REACT_APP_MAIN_INDEX, { replace: true });
  }, []);

  return (
    <div>
    </div>
  )
}
export default IndexPage

export const Head = () => <title>송지훈 블로그 HOME</title>